import * as React from 'react'
import { graphql } from 'gatsby'
import { GatsbyImage, getImage } from 'gatsby-plugin-image'
import Layout from '../components/layout'
import Seo from '../components/seo'


const RecipePost = ({ data, children }) => {
  return (
    <Layout pageTitle={data.Drupal.nodeRecipes.nodes.title}>
      <p>{data.Drupal.nodeRecipes.nodes.preparationTime}</p>
      {children}
    </Layout>
  )
}

export const query = graphql`
  query {
    Drupal {
      nodeRecipes(first: 10) {
        nodes {
          changed
          id
          cookingTime
          created
          path
          status
          title
          preparationTime
          numberOfServings
          recipeInstruction {
            format
            processed
            value
          }
          ingredients
        }
      }
    }
  }
`

export const Head = ({ data }) => <Seo title={data.Drupal.nodeRecipes.nodes.title} />

export default RecipePost